export default {
  load: function (array_img) {
    let promises = 
      array_img
        .filter(o=>typeof o == 'string')
        .map(url => {
          return new Promise((resolve, reject) => {
            let i;
            if (url.indexOf("mp4")!=-1){
              i = new Video()
              
            }else if (url.indexOf("mp3")!=-1){
              i = new Audio()
              i.preload="auto"
            }else if (url.indexOf("wav")!=-1){
              i = new Audio()
              i.preload="auto"
            }else{
              i = new Image()
            }
            
            i.onloadeddata = function () {
              // console.log("loaded")
              resolve("ok")
              console.log("Peloaded: " + url)
            }
            i.onload = function () {
              // console.log("loaded")
              resolve("ok")
              console.log("Peloaded: " + url)
            }
            i.onerror = function(err){
              console.error("Peload Fail: " +url)
              reject("ohh")

            }
            i.src = url
          })
        })
    return Promise.all(promises)
  }
}